.WinDialog {
  position:absolute;
  left: 30px;
  right: 30px;
  top: 30px;
  bottom: 30px;
  background-color: beige;
  border: 1px solid sandybrown;
  padding: 8px;
  z-index: 2;
  
  display: flex;
  flex-direction: column;
  align-items: stretch;

  h1 {
    text-align: center;
  }

  & > * {
    padding: 8px;
  }

  p {
    border: 1px solid sandybrown;
    justify-content: end;
    margin: 8px;
    text-align: center;
    border-radius: 8px;
    background-color: white;

    a {
      text-decoration: none;
      color: black;
      font-size: 20px;
    }
  }

  .fill {
    flex-grow: 1;
  }

  button {
    font-size: 20px;
  }
}