
:root {
  --scale: 70px;
  --toySize: calc(var(--scale) - 30px);
  --tubeWidth: calc(var(--scale) - 20px);
  --tubeHeight: calc(var(--tubeWidth) * 4 );
  --tubeRadius: calc(var(--tubeWidth) / 2 );
}

.ToyComp {
  position: absolute;
  width: var(--toySize);
  height: var(--toySize);
  margin: 5px;
  border: 1px solid black;
  border-radius: 50%;
  
  transition: top 0.4s, left 0.4s;
  text-align: center;
  font-size: calc(var(--toySize) - 10px);
}


.TubeComp {
  position: absolute;
  width: var(--tubeWidth);
  height: var(--tubeHeight); // 120
  border-width: 2px;
  border-style: solid;
  border-color: transparent black black black;
  border-radius: 0 0 var(--tubeRadius) var(--tubeRadius);
  z-index: 1;
  
}

